.table {
    .cellWrapper {
      display: flex;
      align-items: center;
  
      .image {
        width: 42px;
        height: 32px;
        border-radius: 5px;
        margin-right: 10px;
        object-fit: cover;
      }
      .p-desc{
        display: flex;
        flex-direction: column;

        .prod{
            font-weight: 700;
        }
        .desc{
            color: rgb(190, 185, 185);
            font-size: 12px;
            font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
        }
      }
    }
}  